.allposts_wrapper {
	background-color: #def2f1;

	.backbutton {
		a {
			display: inline-block;
			transform: translate(10px, 10px);
			color: #2b7a78;
			text-decoration: none;
			font: {
				size: 20px;
				family: 'Roboto Mono', monospace; }

			img {
				width: 30px;
				height: auto; }

			&:hover {
				position: relative;
				left: -0.2vw;
				color: rgba(0,0,0,0.4); } } }

	.allposts_inner {
		max-width: 800px;
		margin: 0 auto;
		padding: 50px 20px;

		h2 {
			width: 100%;
			margin: 7px 3px;
			text-align: left;
			font: {
				size: 50px;
				family: 'Roboto Mono', monospace; }

			img {
				width: 7%;
				height: auto; } }

		span {
			display: flex;
			flex-direction: column;
			justify-content: space-between;

			a {
				background-color: #fefefe;
				color: black;
				text-decoration: none;
				padding: 1.3vw;
				margin: 3.5px;
				font: {
					size: 20px;
					family: 'Roboto Mono', monospace; }
				border: {
					bottom: 2px solid black;
					right: 2px solid black; }

				img {
					width: 5%;
					height: auto; }

				&:hover {
					position: relative;
					left: 0.1vw;
					border: {
						bottom: 0.6px solid black;
						right: 0.6px solid black; }
					color: rgba(0,0,0,0.4); } } } } }
