.post_wrapper {
	background-color: #ffffff;

	.backbutton {
		a {
			display: inline-block;
			transform: translate(10px, 10px);
			color: #2b7a78;
			text-decoration: none;
			font: {
				size: 20px;
				family: 'Roboto Mono', monospace; }

			img {
				width: 30px;
				height: auto; }

			&:hover {
				position: relative;
				left: -0.2vw;
				color: rgba(0,0,0,0.4); } } }

	.post_inner {
		max-width: 850px;
		margin: 0 auto;
		padding: 0 25px;

		.main_info {
			background-color: #def2f1;
			margin: 30px 0;
			padding: 5px 15px;
			border-bottom: 1px solid;
			font-family: 'Roboto Mono', monospace;
			h1 {
				font-size: 35px; }
			h3 {
				font: {
					size: 20px;
					weight: 300; } }
			p {
				text-align: right;
				font: {
					size: 15px;
					family: 'Open Sans', sans-serif; } } }

		.main_post {
			font-family: 'Open Sans', sans-serif;
			a {
				text-decoration: none;
				color: #f76c6c;
				font: {
					size: 15px;
					family: 'Roboto Mono', monospace; }

				&:hover {
					color: rgba(0,0,0,0.4); } }

			h2 {
				font-size: 30px;
				font-family: 'Roboto Mono', monospace; }
			h3 {
				font-size: 25px;
				font-family: 'Roboto Mono', monospace; }
			p {
				font-size: 16px;
				overflow-x: scroll; } } } }









