.footwrapper {
	background-color: #ffffff;
	.foot-inner {
		max-width: 800px;
		margin: 0 auto;
		padding: 40px 20px;
		display: flex;
		flex-direction: row;

		.left {
			width: 50%;
			h2 {
				margin: 10px 3px 0px 3px;
				font: {
					size: 30px;
					family: 'Roboto Mono', monospace; }

				img {
					width: 10%;
					height: auto; } }

			ul {
				font: {
					size: 20px;
					family: 'Roboto Mono', monospace; }

				li {
					padding: 5px 0; }
				a {
					text-decoration: none;
					color: #f76c6c;
					&:hover {
						color: rgba(0,0,0,0.4); } } } }

		.right {
			width: 50%;
			transform: translateY(40px);
			text-align: right;
			margin: auto;
			color: #d9d9d9;
			p {
				font: {
					size: 9px;
					family: 'Roboto Mono', monospace; } } } } }


@media screen and (max-width: 780px) {
	.footwrapper {
		.foot-inner {
			flex-direction: column;
			.left {
				width: 100%;
				h2 {
					img {
						width: 6%;
						height: auto; } } }
			.right {
				border-top: 0.4px solid #cccccc;
				width: 100%;
				text-align: center; } } } }
